<template>
  <div :id="domId"></div>
</template>

<script type="text/ecmascript-6">

  export default {
    mounted () {
      this.domId = this.getUID(this.domId);
      let config = {};

      // 
      Object.assign(config, this.defaultConfig, this.config);
      config.toolbars = this.toolbars.length > 0 ? this.toolbars : this.defaultToolbars;

      //  DOM  Id  DOM 
      this.$nextTick(() => {
        this.init(this.domId, config);
      });
    },
    props: {
      content: {
        type: String,
        default: ''
      },
      config: {
        type: Object,
        default: () => {
            return {};
        }
      },
      toolbars: {
        type: Array,
        default: () => {
          return [];
        }
      }
    },
    data () {
      return {
        ue: null,
        domId: 'J_UEditor',
        defaultToolbars: [ // 
          [
            'bold', // 
            'italic', // 
            'fontsize', // 
            'underline', // 
            'forecolor', // 
            'justifyleft', // 
            'justifycenter', // 
            'justifyright', // 
            'justifyjustify', // 
            'lineheight', // 
            'removeformat', // 
            'source' // 
          ]
        ],
        defaultConfig: { // ueditor 
          serverUrl: '', // 
          zIndex: 1050, //  900 modal-backdrop  z-index = 1040
          initialFrameWidth: '100%', // 
          initialFrameHeight: 200, //  px
          elementPathEnabled: false, // 
          autoClearinitialContent: false, // 
          wordCountMsg: '{#count}, {#leave}&emsp;' // 
        }
      }
    },
    methods: {
      getUID (prefix) {
        do {
            prefix += ~~(Math.random() * 1000000);
        }
        while (document.getElementById(prefix));

        return prefix;
      },
      init (domId, config) {
        this.ue = UE.getEditor(domId, config);
        this.bind();
      },
      bind () {
        //  content
        this.ue.ready(() => {
            this.ue.setContent(this.content);
        });

        // emit  change 
        this.ue.addListener('contentChange', () => {
          this.$emit('change', this.getContent());
        });
      },
      reset () {
        this.ue.reset();
        this.ue.setContent('');
      },
      destroy () {
        this.ue.destroy();
      },
      setContent (content) {
        this.ue.setContent(content);
      },
      getContent () {
        return this.ue.getContent();
      }
    },
    watch: {
        content (val) {
          this.ue.setContent(val);
        }
    }
  }
</script>

<style lang="scss" scoped>

</style>



